$delete-color: #881234;

.todo {
    &-grid {
        margin: 2rem auto;
        width: 80%;
        height: auto;
        display: flex;
        flex-direction: column;
        // background: pink;
    } // end of grid

    &-item {
        height: auto;
        padding: 1rem;
        margin: 0.5rem;
        border-radius: 10px;
        transition: all 0.2s ease-in-out;

        // content positioning 
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;

        &:hover {
            box-shadow: 0 0px 10px rgba(0, 0, 0, 0.3);
            transform: scale(1.01);
        }
        .delete-btn {
            // color: $delete-color !important;
        }
    } // end of item
}